.article-employees{
  margin-bottom: 2rem;
  background-color: #fff;
  box-shadow: 0 5px 5px gray;
  border-radius: 0 0 10px 10px;
  transition: transform .3s, box-shadow .3s;
}
.employees__img{
  display: block;
  width: 100%;
  height: 190px;
  object-fit: contain;
}
.content__title,
.content__text,
.content__age{
  text-align: center;
}